import './App.css';
import axios from "axios"
import React, { useState, useEffect } from "react"
import ListPhone from './listpersons';
import NewPerson from './NewPerson';
import FilterBy from './filterBy';
import FilterList from './filterList';
import { Notification } from './components/notification';
function App() {
  const [persons, setPersons] = useState([])
  const [newPerson, setNewPerson] = useState({ name: "", number: "", id: null })
  const [Render, setRender] = useState(false)
  const [filter, setFilter] = useState("")
  const [filterList, setFilterList] = useState([])
  const [keyNotification, setKeyNotification]= useState({})

  useEffect(() => {
    function getPersons() {
      axios.get("api/people")
        .then(res => {
          const name = res.data
          setPersons(name)
          setRender(false)
          setKeyNotification({})
        })
    }


    getPersons()
    Render && getPersons()
  }, [Render])

  const handleChange = (e) => {
    const { name, value } = e.target

    setNewPerson(prev => ({
      ...prev,
      [name]: value
    }))
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    const match = persons.filter(person => person.name === newPerson.name.toLocaleLowerCase())
  
    if (match[0] !== undefined) {
      const { name, id } = match[0]
      var answer = window.confirm(` ${name} is already added to the notebook, replace the old number with the new one.`);
      if (answer) {
        axios.put(`api/people/${id}`, {number: newPerson.number })
          .then(() => setNewPerson({ name: "", number: "", id: null }))
          .then(() => setRender(true))
      }
      else {
        console.log("cancel")
      }
    }
     
      else{
        if(newPerson.name.length>1 && newPerson.number.length>1){
        
        if(newPerson.name.length <3){
          setKeyNotification({key:"nameLength", name: newPerson.name})
          setTimeout(() => {
            setRender()
          }, 4000);    
        }
         else if(newPerson.number[2]==="-" || newPerson.number[3] ==="-"){
        axios.post("api/people", {...newPerson, name:newPerson.name.toLocaleLowerCase()})
          .then(response => setPersons(persons.concat(response.data)))
          .then(() => setNewPerson({ name: "", number: "", id: null }))
          .then(()=> {
            setKeyNotification({key:"add", name:newPerson.name})
            setTimeout(() => {
              setRender()
            }, 2000);
          })
        }
        else {
          setKeyNotification({key:"invalidNumber", name:newPerson.number})
          setTimeout(() => {
            setRender()
          }, 4000);    
        }}
        else{
          setKeyNotification({key:"empty", name:"required"})
          setTimeout(() => {
            setRender()
          }, 4000);  
        }
      }}

  
  const handleDelete = (e) => {
    e.preventDefault()
    const currentName = e.target.parentElement.firstChild.lastChild.data
    var answerDelete = window.confirm(` delete ${currentName} ?`);
    if (answerDelete) {
        const currentItemId =e.target.parentElement.id
        
        axios.delete(`api/people/${currentItemId}`)
        .then(()=> setPersons(persons.filter(person => person.id !== currentItemId)))
          .catch(() => {
            setKeyNotification({key:"error", name:currentName})
            setTimeout(() => {
              setRender()
            }, 2000);
          })    
          
      }
      else {
        console.log("cancel")
      }
    
  }
  const submitFilter = (e) => {
    e.preventDefault()
    const filters = persons.filter(person => (person.name.toLowerCase()).startsWith(filter.toLowerCase()) && person)
    setFilterList(filters)
  }

  const handleChangeFilter = (e) => {
    const filter = e.target.value
    setFilter(filter)
  }
  return (
    <div className="App">
      <div className='container'>
        <Notification keyNoti={keyNotification}/>
        <h1>Phone Book</h1>
        <FilterBy changeFilter={handleChangeFilter} submitFilter={submitFilter} />

        <h2>Add a new person</h2>
        <NewPerson newPerson={newPerson} handleChange={handleChange} handleSubmit={handleSubmit} />
        <h2>Numbers</h2>
        {filterList.length > 0
          ? <FilterList filterList={filterList} handleChange={handleChange} />
          : <ListPhone persons={persons} handleDelete={handleDelete} />
        }
      </div>
    </div>
  );
}

export default App;
