export const Notification = ({ keyNoti }) => {
  if (keyNoti === null) {
    return null
  }
  const { key, name } = keyNoti

  return (<>
    {
      key !== undefined &&
      <div className={key}>
        {key === "add" && <p >Added {name}</p>}
        {key === "error" && <p >{name} was already removed from server</p>}
        {key === "nameLength" && <p > {name} is shorter then the minimum allowed(3)</p>}
        {key === "invalidNumber" && <p >{name} is invalid format, eg 09-1234556 and 040-22334455 are valid</p>}
        {key === "empty" && <p > All the form is {name}</p>}

      </div>
    }
  </>
  )
}
